import React, { useEffect, useRef, useState } from "react";
import "./App.css";
import AOS from "aos";
import "aos/dist/aos.css";
import Home from "./components/Home";
import Header from "./components/Header";
import AboutMe from "./components/AboutMe";
import Skills from "./components/Skills";
import Projects from "./components/Projects";
import Contact from "./components/Contact";

const App = () => {
  const [activeSection, setActiveSection] = useState(); //   

  const scrollRef = useRef([]);

  const handleScrollView = (target) => {
    const name = target;

    console.log("", name);
    const category = {
      Home: 0,
      AboutMe: 1,
      Skills: 2,
      Projects: 3,
    };
    scrollRef.current[category[name]].scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    // AOS 
    AOS.init({
      duration: 1200, //   
      easing: "ease-in-out", //   
      once: true, //     
      mirror: true, //     
    });

    // IntersectionObserver 
    const options = {
      root: null, // 
      rootMargin: "0px",
      threshold: 0.5, //  50%  
    };

    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          setActiveSection(entry.target.id);
        }
      });
    }, options);

    //  
    const sections = document.querySelectorAll(".section");
    sections.forEach((section) => observer.observe(section));

    return () => {
      sections.forEach((section) => observer.unobserve(section));
    };
  }, []);

  return (
    <div className="App">
      <Header handleScrollView={handleScrollView} />
      <section id="Home" className="target-element">
        <div
          ref={(el) => {
            scrollRef.current[0] = el;
          }}
        >
          <Home />
        </div>
      </section>
      <section id="AboutMe" className="target-element">
        <div
          ref={(el) => {
            scrollRef.current[1] = el;
          }}
        >
          <AboutMe />
        </div>
      </section>
      <section id="Skills" className="target-element">
        <div
          ref={(el) => {
            scrollRef.current[2] = el;
          }}
        >
          <Skills />
        </div>
      </section>
      <section id="Projects" className="target-element">
        <div
          ref={(el) => {
            scrollRef.current[3] = el;
          }}
        >
          <Projects />
        </div>
      </section>
      <Contact />
    </div>
  );
};

export default App;
